
#app {
  .sl-row {
  }
  .sl-col {
    border: 1px dashed #000000;
    min-height: 100px;
  }

  .sl-container {
    position: relative;
  }

  .sl-col {
    position: relative;
  }
  .sl-block {
    margin: 20px 0;
    position: relative;
    min-height: 100px;
    &:hover {
      box-shadow: 0 0 2px 1px #000000;
    }
  }
}
.btn-xs {
  font-size: 12px;
  line-height: 1rem;
  padding: 0;
  height: 20px;
}

.ghost-sl-block {
  opacity: 0.5;
  height: 100px;
}

.sl-draggable-container {
  height: 100%;
}

.sl-block-dragging {
  width: 100px !important;
  height: 100px !important;
  background-color: grey;
  display: block;
  border: 1px solid black;
  > * {
    display: none;
  }
}

#app .sl-container.sl-dragging {
  .sl-block {
    box-shadow: 0 0 2px 1px #000000;
    &.sl-block-placeholder {
      display: none;
    }
  }
  .sl-col {
    min-height: 140px;
  }
}
