
.sl-col-controls {
  padding: 0.5em 0;
  position: absolute;
  height: 100%;
  left: 0;
  visibility: hidden;
  > .btn-group {
    height: 50%;
    top: 25%;
  }

  &.right {
    right: 0;
    top: 0;
    left: auto;
  }

  .sl-col-add-button {
    padding: 0;
  }
}

.sl-remove-col-controls {
  position: absolute;
  bottom: 0;
  left: 50%;
  bottom: 10px;
  transform: translate(-50%, 0);
  z-index: 1;
  visibility: hidden;
}

.sl-row:hover {
  .sl-col-controls,
  .sl-remove-col-controls {
    visibility: visible;
  }
}
