
.sl-add-row-controls {
  position: relative;
  // visibility: hidden;
  > .btn-group {
    width: 120px;
    left: 50%;
    transform: translate(-50%, 0);
    position: absolute;
    top: -8px;

    button {
      padding: 0;
      line-height: 1rem;

      &:first-child {
        width: 50px;
      }
    }
  }
}
.sl-container:hover {
  .sl-add-row-controls {
    visibility: visible;
  }
}
